import React, { useRef, Suspense } from "react";
import Loader from '../components/Loader'
import Author from "./creative/author";
import About from "./creative/about";
import Contact from "./creative/contact";
import PageSideBar from "./pagesidebar";
import Navigation from "../components/navigation";
const Creative = () => {
  const moonRef = useRef();
  return (
    <Suspense fallback={<Loader />}>
      <div
        className={`creative flex perspect-1 justify-center items-center relative z-10 w-full`}
      >
        <Navigation />
        <PageSideBar />
        <section className="flex-1 flex flex-col items-center">
          <section ref={moonRef} className="relative">
            <Author id="home" />
            <About id="about" />
          </section>
          <Contact id="contact" />
        </section>
      </div>
    </Suspense>
  );
};

export default Creative;
