import React from "react";
import { ScrollParallax,MouseParallax } from "react-just-parallax";
import DesignPartial from "./about/designpartial";
import Gallery from "./about/gallery";
import { useSnapshot } from "valtio";
import state from "../../store";
import TransitionLink from "../../hooks/TransitionLink";
const About = ({ ...props }) => {
  const snap = useSnapshot(state);
  return (
    <div
      className="w-full mt-10 lg:mt-64 flex relative justify-center items-center perspect-1"
      {...props}
    >
      <section className="inline-flex flex-1 relative w-full justify-center items-center flex-col lg:flex-row z-10">
        <div className="flex justify-center items-center w-full relative h-full flex-col xl:flex-row">
          <ScrollParallax strength={0.1} shouldPause={true}>
            <DesignPartial />
          </ScrollParallax>
          <Gallery />
          <MouseParallax isAbsolutelyPositioned={true} zIndex={-1}>
          <TransitionLink to="/island" className="">
          <div className="w-full absolute h-full rotate-12">
            <img
              className="m-auto w-screen object-contain z-[-1] absolute bottom-0 object-left-bottom blur-[1px]"
              name="City"
              src={snap.forground_top}
              alt="city"
              title="click to get a view"
            />
            <img
              className="m-auto w-screen object-contain z-[-1] absolute top-full object-left-top"
              name="mountain base"
              src={snap.forground_bottom}
              alt="mountain base"
              title="click to get a view"
            />
          </div>
          </TransitionLink>
          </MouseParallax>
        </div>
      </section>
    </div>
  );
};

export default About;
