import React from "react";

import { MouseParallax, ScrollParallax } from "react-just-parallax";
import TransitionLink from "../../hooks/TransitionLink";
import { useSnapshot } from "valtio";
import state from "../../store";
import { animateScroll as scroll } from "react-scroll";
import Contactbar from "../../components/contact";
const Contact = ({ ...props }) => {
  const snap = useSnapshot(state);
  return (
    <div name="contact" className="w-full flex z-[1] mt-64" {...props}>
      <div className="Anuphan w-full inline-flex after:items-center justify-center relative min-h-[25vh] mt-64">
        <section className="absolute inline-flex w-[50vw] h-[50vh] bottom-0 justify-center items-center">
          <div className="inline-flex gap-4 z-[50]">
            <ScrollParallax>
              <Contactbar />
            </ScrollParallax>
          </div>
          <TransitionLink to="/moon" className="">
            <img
              className="w-full left-0 absolute object-contain transition-all object-bottom contrast-200 blur-[1px] hover:blur-0 hover:contrast-100 z-0 hover:scale-[1.1] top-full"
              src={snap.background}
              alt="space-man"
              title="moon shot"
            />
          </TransitionLink>
        </section>{" "}
        <MouseParallax strength={-0.06} zIndex={10}>
          <img
            className="absolute plane z-[90] opacity-0 w-0 h-0 lg:w-[140px] lg:h-[140px] object-contain bottom-full left-2/3 object-left-bottom blur-[1px] lg:opacity-100 cursor-pointer"
            src={snap.plane}
            alt="Plane flying"
            title="Off to space"
            onClick={() => {
              scroll.scrollToTop();
              state.thought = !snap.thought;
            }}
          />
        </MouseParallax>
      </div>
      <ScrollParallax isAbsolutelyPositioned={true} zIndex={-1}>
        <img
          className="absolute hand z-[-2] opacity-0 w-0 h-0 lg:w-[640px] lg:h-[640px] object-contain bottom-0 left-0 object-left-bottom blur-[1px] contrast-200 mt-[-10vh] lg:opacity-100"
          src={snap.hand}
          alt="author hand portrait"
          title="picture of author's hand"
        />
      </ScrollParallax>
    </div>
  );
};

export default Contact;
