import React from "react";
import { MouseParallax } from "react-just-parallax";
const DesignPartial = () => {
  return (
    <div className=" max-w-full xl:max-w-6xl z-20 w-full ">
      <MouseParallax strength={0.025}>
        <p className="text-4xl sm:text-5xl xl:text-7xl z-10 ">
          <span className="flex">
            <span className="inline-flex Sedgwick text-gradB design">Work</span>
            <span className="ml-9 Alfa">Hi</span>
            <span className=" Anuphan italic">story</span>
          </span>
        </p>
      </MouseParallax>
    </div>
  );
};

export default DesignPartial;
